import React from 'react';
import { Table, Thead, Tbody, Tr, Th, Td, TableCaption, TableContainer } from '@chakra-ui/react';

export const ScoreTable = () => {
  // Dummy data for demonstration. Replace with real data if available.
  const data = [
    { id: 1, name: 'Player 1', score: 70 },
    { id: 2, name: 'Player 2', score: 68 },
    { id: 3, name: 'Player 3', score: 72 }
  ];

  return (
    <TableContainer>
      <Table variant="simple">
        <TableCaption>Score Table</TableCaption>
        <Thead>
          <Tr>
            <Th>ID</Th>
            <Th></Th>
            <Th isNumeric></Th>
          </Tr>
        </Thead>
        <Tbody>
          {data.map((item) => (
            <Tr key={item.id}>
              <Td>{item.id}</Td>
              <Td>{item.name}</Td>
              <Td isNumeric>{item.score}</Td>
            </Tr>
          ))}
        </Tbody>
      </Table>
    </TableContainer>
  );
}; 