import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styles from '../styles/styles';

export const Sidebar = ({ 
  onAddCourse, 
  onDeleteCourse, 
  onAddRow, 
  onAddDay, 
  onDeleteDay, 
  onOpenTournamentList,
  onCaptureGroupSheet,
  onPrintBusinessCards,
  onOpenParModal,
  onAssignOrder,
  onPrintGroupTable,
  onNewTournament,
  onGenerateTestData,
  onCaptureTable,
  isOpen,
  onToggle,
  division,
  onOpenDivisionModal,
  onDeleteEmptyRows,
  onOpenTitleModal
}) => {
  const [hoveredButton, setHoveredButton] = useState(null);

  const sidebarStyle = {
    ...styles.sidebar,
    transform: `translateX(${isOpen ? '0' : '-150px'})`,
    transition: 'transform 0.3s ease-in-out'
  };

  const toggleButtonStyle = {
    position: 'fixed',
    left: isOpen ? '155px' : '5px',
    top: '80px',
    padding: '8px 12px',
    backgroundColor: hoveredButton === 'toggle' ? '#34495e' : '#2c3e50',
    color: '#fff',
    border: 'none',
    borderRadius: '0 8px 8px 0',
    cursor: 'pointer',
    fontSize: '16px',
    zIndex: 1000,
    transition: 'all 0.3s ease-in-out',
    boxShadow: '3px 0 10px rgba(0,0,0,0.2)',
    transform: hoveredButton === 'toggle' ? 'scale(1.1)' : 'scale(1)',
    width: '40px',
    height: '40px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontWeight: 'bold'
  };

  const getButtonStyle = (buttonName) => ({
    ...styles.button,
    transform: hoveredButton === buttonName ? 'scale(1.05)' : 'scale(1)',
    boxShadow: hoveredButton === buttonName ? '0 2px 5px rgba(0,0,0,0.2)' : 'none',
    transition: 'all 0.2s ease-in-out'
  });

  const divisionToggleStyle = {
    ...styles.button,
    backgroundColor: division === '' ? '#007aff' : '#ff2d55',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: '5px'
  };

  return (
    <>
      <button
        className="print-hide"
        style={toggleButtonStyle}
        onClick={onToggle}
        onMouseEnter={() => setHoveredButton('toggle')}
        onMouseLeave={() => setHoveredButton(null)}
      >
        {isOpen ? '<<' : '>>'}
      </button>
      <div style={sidebarStyle} className="print-hide">
        <button 
          style={getButtonStyle('addRow')}
          onClick={onAddRow}
          onMouseEnter={() => setHoveredButton('addRow')}
          onMouseLeave={() => setHoveredButton(null)}
        >
           
        </button>
        <button 
          style={getButtonStyle('addCourse')}
          onClick={onAddCourse}
          onMouseEnter={() => setHoveredButton('addCourse')}
          onMouseLeave={() => setHoveredButton(null)}
        >
          <span style={{ color: '#FFD700' }}></span> 
        </button>
        <button 
          style={getButtonStyle('deleteCourse')}
          onClick={onDeleteCourse}
          onMouseEnter={() => setHoveredButton('deleteCourse')}
          onMouseLeave={() => setHoveredButton(null)}
        >
          <span style={{ color: '#FFD700' }}></span> 
        </button>
        <button 
          style={getButtonStyle('addDay')}
          onClick={onAddDay}
          onMouseEnter={() => setHoveredButton('addDay')}
          onMouseLeave={() => setHoveredButton(null)}
        >
           
        </button>
        <button 
          style={getButtonStyle('deleteDay')}
          onClick={onDeleteDay}
          onMouseEnter={() => setHoveredButton('deleteDay')}
          onMouseLeave={() => setHoveredButton(null)}
        >
           
        </button>
        <button 
          style={{...getButtonStyle('addPar'), marginBottom: '20px'}}
          onClick={onOpenParModal}
          onMouseEnter={() => setHoveredButton('addPar')}
          onMouseLeave={() => setHoveredButton(null)}
        >
            
        </button>
        <button
          style={getButtonStyle('tournamentList')}
          onClick={onOpenTournamentList}
          onMouseEnter={() => setHoveredButton('tournamentList')}
          onMouseLeave={() => setHoveredButton(null)}
        >
           
        </button>
        <button
          style={getButtonStyle('openTitleModal')}
          onClick={onOpenTitleModal}
          onMouseEnter={() => setHoveredButton('openTitleModal')}
          onMouseLeave={() => setHoveredButton(null)}
        >
            
        </button>
        <button
          style={getButtonStyle('captureGroupSheet')}
          onClick={onCaptureGroupSheet}
          onMouseEnter={() => setHoveredButton('captureGroupSheet')}
          onMouseLeave={() => setHoveredButton(null)}
        >
           
        </button>
        <button 
          style={getButtonStyle('printBusinessCards')}
          onClick={onPrintBusinessCards}
          onMouseEnter={() => setHoveredButton('printBusinessCards')}
          onMouseLeave={() => setHoveredButton(null)}
        >
          
        </button>
        <button 
          style={getButtonStyle('printGroupTable')}
          onClick={onPrintGroupTable}
          onMouseEnter={() => setHoveredButton('printGroupTable')}
          onMouseLeave={() => setHoveredButton(null)}
        >
           
        </button>
        <button
          style={getButtonStyle('division')}
          onClick={onOpenDivisionModal}
          onMouseEnter={() => setHoveredButton('division')}
          onMouseLeave={() => setHoveredButton(null)}
        >
            
        </button>
        <button 
          style={{...styles.button, marginBottom: '20px'}} 
          onClick={onAssignOrder}
        >
          
        </button>
        <button
          style={getButtonStyle('captureTable')}
          onClick={onCaptureTable}
          onMouseEnter={() => setHoveredButton('captureTable')}
          onMouseLeave={() => setHoveredButton(null)}
        >
            
        </button>
        <button 
          style={{...styles.button, backgroundColor: '#4CAF50'}}
          onClick={onGenerateTestData}
          onMouseEnter={() => setHoveredButton('generateTest')}
          onMouseLeave={() => setHoveredButton(null)}
        >
            
        </button>
        <button 
          style={{...styles.button, backgroundColor: '#ff3b30'}}
          onClick={onNewTournament}
          onMouseEnter={() => setHoveredButton('newTournament')}
          onMouseLeave={() => setHoveredButton(null)}
        >
            
        </button>
        <button
          style={{...styles.button, backgroundColor: '#ff3b30'}}
          onClick={() => {
            if(window.confirm(' (   ) ?')) {
              onDeleteEmptyRows && onDeleteEmptyRows();
            }
          }}
          onMouseEnter={() => setHoveredButton('deleteEmptyRows')}
          onMouseLeave={() => setHoveredButton(null)}
        >
          
        </button>
      </div>
    </>
  );
};

Sidebar.propTypes = {
  onAddCourse: PropTypes.func.isRequired,
  onDeleteCourse: PropTypes.func.isRequired,
  onAddRow: PropTypes.func.isRequired,
  onAddDay: PropTypes.func.isRequired,
  onDeleteDay: PropTypes.func.isRequired,
  onOpenTournamentList: PropTypes.func.isRequired,
  onCaptureGroupSheet: PropTypes.func.isRequired,
  onPrintBusinessCards: PropTypes.func.isRequired,
  onOpenParModal: PropTypes.func.isRequired,
  onAssignOrder: PropTypes.func.isRequired,
  onPrintGroupTable: PropTypes.func.isRequired,
  onNewTournament: PropTypes.func.isRequired,
  onGenerateTestData: PropTypes.func.isRequired,
  onCaptureTable: PropTypes.func.isRequired,
  isOpen: PropTypes.bool.isRequired,
  onToggle: PropTypes.func.isRequired,
  division: PropTypes.string.isRequired,
  onOpenDivisionModal: PropTypes.func.isRequired,
  onDeleteEmptyRows: PropTypes.func,
  onOpenTitleModal: PropTypes.func.isRequired
}; 