import React, { useState } from 'react';
import { Modal, ModalOverlay, ModalContent, ModalHeader, ModalBody, ModalFooter, Button, Input, Text } from '@chakra-ui/react';
import PropTypes from 'prop-types';

export const PasswordModal = ({ isOpen, onSubmit }) => {
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = () => {
    // Check against the designated password, e.g., 'pkgolf'
    if (password === 'pkgolf') {
      onSubmit();
    } else {
      setError('  .');
    }
  };

  return (
    <Modal isOpen={isOpen} onClose={() => {}} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader> </ModalHeader>
        <ModalBody>
          <Input
            placeholder=" "
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          {error && <Text color="red.500" mt={2}>{error}</Text>}
        </ModalBody>
        <ModalFooter>
          <Button colorScheme="blue" onClick={handleSubmit}></Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};

PasswordModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onSubmit: PropTypes.func.isRequired,
}; 