import React, { useState } from 'react';
import { Box, Button, Heading, Text } from '@chakra-ui/react';
import { Sidebar } from './components/Sidebar';
import { PasswordModal } from './components/PasswordModal';
import { ScoreTable } from './components/ScoreTable';
import { GenericModal } from './components/GenericModal';

function App() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [view, setView] = useState("table");
  const [activeModal, setActiveModal] = useState(null);

  const toggleSidebar = () => setIsSidebarOpen(!isSidebarOpen);

  const onPrintGroupTable = () => {
    setView("table");
  };

  // Instead of alert pop-ups, set activeModal to display the corresponding modal
  const onAddCourse = () => setActiveModal("  ");
  const onDeleteCourse = () => setActiveModal("  ");
  const onAddRow = () => setActiveModal("  ");
  const onAddDay = () => setActiveModal("  ");
  const onDeleteDay = () => setActiveModal("  ");
  const onOpenTournamentList = () => setActiveModal("  ");
  const onCaptureGroupSheet = () => setActiveModal("   ");
  const onPrintBusinessCards = () => setActiveModal("  ");
  const onOpenParModal = () => setActiveModal("  ");
  const onAssignOrder = () => setActiveModal("  ");
  const onNewTournament = () => setActiveModal("   ");
  const onGenerateTestData = () => setActiveModal("   ");
  const onCaptureTable = () => setActiveModal("  ");
  const onOpenDivisionModal = () => setActiveModal("  ");
  const onOpenTitleModal = () => setActiveModal("  ");
  const onDeleteEmptyRows = () => setActiveModal("   ");

  return (
    <Box position="relative">
      <PasswordModal isOpen={!isAuthenticated} onSubmit={() => setIsAuthenticated(true)} />
      <Box display="flex">
        <Sidebar 
          isOpen={isSidebarOpen} 
          onToggle={toggleSidebar}
          onAddCourse={onAddCourse}
          onDeleteCourse={onDeleteCourse}
          onAddRow={onAddRow}
          onAddDay={onAddDay}
          onDeleteDay={onDeleteDay}
          onOpenTournamentList={onOpenTournamentList}
          onCaptureGroupSheet={onCaptureGroupSheet}
          onPrintBusinessCards={onPrintBusinessCards}
          onOpenParModal={onOpenParModal}
          onAssignOrder={onAssignOrder}
          onPrintGroupTable={onPrintGroupTable}
          onNewTournament={onNewTournament}
          onGenerateTestData={onGenerateTestData}
          onCaptureTable={onCaptureTable}
          division=""
          onOpenDivisionModal={onOpenDivisionModal}
          onOpenTitleModal={onOpenTitleModal}
          onDeleteEmptyRows={onDeleteEmptyRows}
        />
        <Box flex="1" ml={isSidebarOpen ? "180px" : "30px"} p={4}>
          <Button onClick={toggleSidebar} mb={4}>Toggle Sidebar</Button>
          <Heading as="h1" size="2xl" textAlign="center">Park Golf Frontend</Heading>
          <Text textAlign="center" mb={4}>     Chakra UI  .</Text>
          {view === "table" && <ScoreTable />}
        </Box>
      </Box>
      {activeModal && (
        <GenericModal 
          isOpen={true} 
          title={activeModal} 
          content={` ${activeModal}   `} 
          onClose={() => setActiveModal(null)} 
        />
      )}
    </Box>
  );
}

export default App; 